.cardItem-container {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    cursor: pointer;
    padding: 25px;
    margin: 10px auto;
    min-width: 250px;
    max-width: 250px;
    height: 300px;
    background-color: #fff;
    box-shadow: 5px 5px 25px rgb(184, 183, 183);
  
    .image {
      width: 200px !important;
      height: 160px !important;
    }
  
    .name {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  
    .price-text {
      color: #db2828;
      font-size: 20px;
      font-weight: 600;
    }
  }