.home-container {
  .filter {
    width: 70%;
    margin: 0 auto;
    display: flex;
    // justify-content: space-around;
    padding-top: 85px;
    padding-bottom: 5px;
    flex-wrap: wrap;

    .search {
      margin-left: 5px;
    }

    .selectForm {
      display: flex;
      justify-content: start;
      margin-top: 10px;
    }

    .selectBox {
      padding: 5px;
      margin: 0 0 0 5px;
      width: 150px;
      height: 36px;
      margin-top: -9px;
    }
  }
  .currentUser {
    top: 90px;
    right: 20px;
    position: absolute;



    span {
      font-weight: 700;
      color: #0bb102;
    }
  }
  .imgBanner {
    width: 100%;
    height: 350px;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .container-body {
    display: flex;
    justify-content: space-between;
  }

  .menuLeft {
    width: 15%;
    padding-top: 80px;
    padding-left: 10px;
  }

  .menuRight {
    width: 15%;
    padding-top: 80px;
  }

  .product {
    flex-wrap: wrap;
    flex: start;
    display: flex;
    padding-top: 180px;
    width: 80%;
    z-index: 1;
    border: none !important;

    .noResults {
      width: 100%;
    }
  }
  .Paginator{
    text-align: center !important;
  }
}